import { Space, Table, Tag } from 'antd';
import React,{ Component } from 'react';
import './index.css'
const { Column, ColumnGroup } = Table;

class App extends Component {


  constructor(props){
    super(props)
    this.state={
      data:{}
    }
  }


  render() {
      return (
        <div className='table'>
        <Table dataSource={this.props.data} pagination ={{hideOnSinglePage:true}} >
          <Column title="姓名" dataIndex="firstName" key="firstName" />
          <Column title="工作地点" dataIndex="address" key="address" />
        </Table>
        </div>
      )
  }
}



export default App;

