import { Input,Text,Radio,message } from 'antd';
import axios from 'axios';
import React,{ Component } from 'react';
import Table from './table'
import './index.css'
const { Search } = Input;
const baseUrl = 'http://localhost:8080/edu/teacher'
const data={};
class App extends Component {
 

  state={
      value:"",
      data:[],
      role:1
  }
  handleValue(e){
    console.log(e.target.value)
    this.setState({
      value:e.target.value
      
    })
  }
  onSearch(){

    // 模拟数据
    // this.setState({
    //   data:[
    //     {
    //     firstName: 'John',
    //     address: 'New York No. 1 Lake Park',
    //     },
    //     {
    //       firstName: 'Jim Green',
    //       address: 'London No. 1 Lake Park',

    //     },
    //     {
    //       firstName: 'Joe Black',
    //       address: 'Sidney No. 1 Lake Park',
    //     },
    //   ]
      
    // })
     
     
      
     console.log(this.state.value)
     axios.post(baseUrl,
      {
        "username": this.state.value,
         "code":this.state.role
      }
      ).then(res=>{
       console.log(res)
       if(res.data.code!=200){
        message.info('查询失败，请查看姓名是否输错或者，工作人员选错')
       }else{
       this.setState({
        data:[
          {
            firstName :res.data.data.username,
            address: res.data.data.seat
          }
          
        ]
       })
      }
     },function(res){
       console.log(res)
       message.info('查询失败，请查看姓名是否输错或者，工作人员选错')
     })
  }

  handleRole(e){
    console.log(e.target.value)
    this.setState({
      role:e.target.value
    })
  }



  render() {
    return(
  <>
  {/* <div className='search'>怀化学院监考点查询</div> */}
  <div className='zong'>
    <div className='zongRadio'>
    <Radio.Group onChange={this.handleRole.bind(this)} defaultValue={1}>
      <Radio value={1} >监考老师</Radio>
      <Radio value={2} >其他工作人员</Radio>
    </Radio.Group>
    </div>
    <div className='zongSearch'>
    <Search placeholder="请输入您的姓名" enterButton="Search" 
    size="large" allowClear={true} value={this.state.value} onChange={this.handleValue.bind(this)} onSearch={this.onSearch.bind(this)}/>
    </div>
    <div className='zongTable'>
    <Table data={this.state.data}></Table>
    </div>
  </div>
  </>
    )}
}

export default App;